.success-stories-content {
  font-family: $base-font-family;

  .stories-nav {
    .elementor-nav-menu__container {
      .elementor-nav-menu {
        > .menu-item {
          font-family: $base-font-family;

          > .elementor-item {
            background: $grey-light;
            &:hover {background: darken($grey-light, 5%);}

            .sub-arrow {
              position: absolute;
              right: 20px;
              top: 0;
              bottom: 0;
              margin: auto;
              display: flex !important;
              align-items: center;

              i {display: none;}

              &:after {
                content: '';
                width: 0;
                height: 0;
                border-left: 9px solid transparent;
                border-right: 9px solid transparent;
                border-bottom: 10px solid $green;
                transition: $default-transition;
              }
            }
          }


          > .elementor-item.highlighted {
            .sub-arrow {
              &:after {transform: rotateX(180deg);}
            }
            + .sub-menu {
              //display: block !important;
            }
          }


          .sub-menu {
            //display: none !important;
            margin: 0 !important;
            padding: 10px 0;
            position: static;
            width: 100%;
            a {
              border: none;

              font-family: $base-font-family;
              padding: 5px 0 5px 20px;
            }
          }
        }//> .menu-item
      }//.elementor-nav-menu
    }//.elementor-nav-menu__container
  }//.stories-nav


  .stories-posts {

    &.elementor-posts__hover-gradient .elementor-post__card {
      .elementor-post__thumbnail__link:after {
        background: rgba(30,50,88,.4);
      }
      &:hover {
        .elementor-post__thumbnail__link:after {
          opacity: 0;
        }
      }
    }

    .elementor-post {


      .elementor-post__thumbnail__link {
        margin-bottom: 0;
      }

      .elementor-post__badge {
        font-family: $base-font-family;
        font-size: 16px;
        font-weight: bold;
      }

      .elementor-post__text {

        font-family: $base-font-family;
        padding-bottom: 20px;
      }

      .elementor-post__title {
        background: $blue-dark;
        font-family: $base-font-family;
        margin: 0 -20px 15px;
        padding: 8px 20px;

        a {
          color: #fff;
          font-family: $base-font-family;
          font-size: 21px;
        }
      }

      .elementor-post__excerpt p {
        color: $blue-dark;
        font-family: $base-font-family;
        font-size: 16px;
        line-height: 1.68;
      }

      .elementor-post__meta-data {
        color: #fff;
        font-family: $base-font-family;
        line-height: 40px;
        padding: 20px;
        position: absolute;
        top: 0;
        right: 0;
      }
    }
  }//.stories-posts

}//.success-stories-content


.success-stories-filter {
  font-size: 16px;

  .category-title {
    align-items: center;
    background: $grey-light;
    color: $main-color;
    display: flex;
    justify-content: space-between;
    padding: 15px 20px;
    &:hover {background: darken($grey-light, 5%);}
    &.active {
      .menu-arrow {transform: rotateX(180deg);}
    }
    .menu-arrow {
      width: 0;
      height: 0;
      border-left: 9px solid transparent;
      border-right: 9px solid transparent;
      border-bottom: 10px solid #68AF7C;
      transition: all 0.3s linear;
    }
  }//.category-title

  ul {
    list-style: none;
    margin: 0;
    a {
      display: block;
      font-weight: bold;
    }
  }

  li ul {
    display: none;
    padding: 10px 0;
    a {padding: 5px 0 5px 20px;}
  }


}//.success-stories-filter